<template>
  <div style="width:800px;height: auto">
    <criteria-paged v-ref:page2 :model="model2" :pager='false'>
      <criteria v-ref:querenka partial='criteria' @condition-changed='search'>
        <div novalidate class="form-inline auto" partial>
          <div class="form-group">
            <!--<span class="btn btn-default" @click="search()"></span>-->
            <!--<report-print id='shexianss' top='1cm' left='1cm' width='100%' height='100%'></report-print>-->
            <div class="col-sm-12">
              <div class="col-sm-10"></div>
              <div class="col-sm-2">
                <report-print @click="$parent.$parent.$parent.updateNumber()" id='shexianss2' top='1cm' left='1cm' width='100%'
                              height='100%' v-ref:reportprint></report-print>
              </div>
            </div>
          </div>
        </div>
      </criteria>
      <div partial='list' id='shexianss2'>
        <table class='tablesty' style="margin: 0px auto;" border="1">
          <tr>
            <th colspan='4' style="font-weight: normal;">
              {{{ model.data.substring(26,model.data.length-8) }}}
              <!--{{model.data}}-->
            </th>
          </tr>
        </table>
      </div>
    </criteria-paged>
  </div>
</template>

<script>
  import {DataModel} from 'vue-client'

  export default {
    title: "确认卡",
    props: ['billselect','billinfo'],
    data() {
      return {
        model2: new DataModel('rs/report/gaizhuangka', {
          data: {
            processid: this.billselect.f_process_id,
            billinfo: this.billinfo,
            billselect: this.billselect
          }
        }),

      }
    },
    // ready() {
    //   this.$refs.page2.$refs.querenka.search()
    // },
    methods: {
      load(){
        this.model2 = null
        this.model2 = new DataModel('rs/report/gaizhuangka', {
          data: {
            processid: this.billselect.f_process_id,
            billinfo: this.billinfo,
            billselect: this.billselect
          }
        })
      }
    },
    watch:{
      'model2'(){
        if(this.model2){
          this.$refs.page2.$refs.querenka.search()
        }
      }
    }
  }
</script>

<style scoped>

</style>



